@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body{
  background-color: rgb(241, 240, 240);
}

.users {
  max-width: 700px;
  margin: 25px auto;
  padding: 15px;
}


.container{
  display: flex;
  justify-content: space-evenly;
  gap: 2rem;
}

.user-item {
  width: 100%;
  padding: 10px 20px;
  display: flex;
  flex-direction: row;
  column-gap: 2rem;
  margin-bottom: 10px;
  border: 1px solid rgb(0, 128, 255);
  border-left: 5px solid rgb(0, 128, 255);
  border-radius: 5px;
  align-items: center;
}

.button-box {
  display: flex;
  flex-direction: row;
  column-gap: 0.5rem;
  align-items: center;
}
.user-form {
  border: 1px solid rgba(128, 128, 128, 0.5);
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.user-form input {
  height: 28px;
  padding: 8px;
  margin-bottom: 10px;
}

.user-form button {
  padding: 8px;
  margin-bottom: 10px;
  cursor: pointer;
}

.user-item button {
  padding: 5px 15px;
  margin-top: 10px;
  cursor: pointer;
}

button {
  background-color: rgb(0, 162, 255);
  border: none;
  border-radius: 20px;
  display: flex;
  color: black;

}


.progress, .todo, .done{
  padding: 40px;
  border-radius: 10px;
  background-color: white;
}


.box{
  width: 100%;
  margin-bottom: 20px;
}

.todo .box{
  border-top: 2px solid lightcoral;
}

.progress .box{
  border-top: 2px solid lightseagreen;
}

.done .box{
  border-top: 2px solid lightgreen;
}

span{
  opacity: 0.3;
  margin-left: 5px;
  margin-right: 5px;
}